/* .container */
.container{
    width:960px;
    max-width:100%;
    padding-left:1em;
    padding-right:1em;
    margin-left:auto;
    margin-right:auto;
}
@media screen and (min-width:1080px) {
    .container{
        width: 1024px;
    }
}

/* .row */
.row{
    margin:0 -1em;
    padding-left: .5em;
    padding-right: .5em;
}

/* .col */
.col,
[class*="col-"]{
    float: left;
    padding-left: .5em;
    padding-right: .5em;
}
.col-0 {display:none}
.col-1 {width:4.166667%}
.col-2 {width:8.333333%}
.col-3 {width:12.5%}
.col-4 {width:16.666667%}
.col-5 {width:20.833333%}
.col-6 {width:25%}
.col-7 {width:29.166667%}
.col-8 {width:33.333333%}
.col-9 {width:37.5%}
.col-10{width:41.666667%}
.col-11{width:45.833333%}
.col-12{width:50%}
.col-13{width:54.166667%}
.col-14{width:58.333333%}
.col-15{width:62.5%}
.col-16{width:66.666667%}
.col-17{width:70.833333%}
.col-18{width:75%}
.col-19{width:79.166667%}
