/* clearfix */
.container,
.row,
[class*="col-"]{
    *zoom:1
}
.container:before,
.container:after,
.row:before,
.row:after,
[class*="col-"]:before,
[class*="col-"]:after{
        content:"";
        /*display:table 可避免父子 margin 合并， block不行*/
        display:table;
        clear:both
}