.button,
button {
  height: 30px;
  padding: 0 1em;
  border: solid 1px #ddd;
  border-radius: 3px;
  background: #fff;
  cursor: pointer;
  transition: 0.3s border, box-shadow;
}
.button:hover,
button:hover {
  border: solid 1px #aaa;
  z-index: 1;
}
.button:focus,
button:focus {
  border: solid 1px #aaa;
  box-shadow: 0px 0px 5px #ddd;
  z-index: 2;
}
.button:active,
button:active {
  box-shadow: 0px 0px 5px #ddd inset;
  z-index: 3;
}
.button.active,
button.active {
  background: #0af;
  color: #fff;
  z-index: 4;
}
.button-group {
  display: inline-flex;
}
.button-group > * {
  position: relative;
  margin-right: -1px;
}
.button-group > *:not(:first-child) {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.button-group > *:not(:last-child) {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
